import React from 'react'
import './POPOSFeature.css'

/*
- üå≤ - outdoors
- ‚òïÔ∏è - coffee
- üñº - art
- üöΩ - toilet
- üîå - power
*/

function getFeature(str) {
	switch(str) {
		case 'outdoors': 
			return 'üå≤'
		case 'coffee':
			return '‚òïÔ∏è'
		case 'art':
			return 'üñº'
		case 'toilet':
			return 'üöΩ'
		case 'power':
			return 'üîå'
		default: 
			return 'Ôºü'
	}
}	

function POPOSFeature(props) {
	const emoji = getFeature(props.name)
	return <div className="POPOSFeature">{emoji}</div>
}

export default POPOSFeature
